/**
 *  Проверка на наличие стартовых значений / Function assess the existance of need values
 */

const firstStart = () => {
    if(getScore() === undefined || parseInt(getScore()) === 0 || !isNumber(parseInt(getScore()))) { 
        localStorage.setItem('score', 0) 
    };

    if(getItems() === undefined || !isObject(JSON.parse(getItems()))) {
        localStorage.setItem('items', JSON.stringify({
            farm: 0,
            factory: 0,
            mine: 0,
            country: 0
        }));
    }
    if(getPrices() === undefined || !isObject(JSON.parse(getPrices()))) { 
        localStorage.setItem('prices', JSON.stringify({
            farm: { 
                price: 100,
                boost: 2 
            },
            factory: {
                price: 500,
                boost: 10
            },
            mine: {
                price: 2000,
                boost: 100
            },
            country: {
                price: 10000,
                boost: 500
            }
        }));
    }    
    if(getData() === undefined || !isObject(JSON.parse(getData()))) { 
        localStorage.setItem('data', JSON.stringify({
            auto: 0,
            perClick: 1,
        }));
    }
}

const getScore = () => {
    return localStorage.getItem('score') !== null ? localStorage.getItem('score') : undefined;
}

const getItems = () => {
    return localStorage.getItem('items') !== null ? localStorage.getItem('items') : undefined;
}

const getPrices = () => {
    return localStorage.getItem('prices') !== null ? localStorage.getItem('prices') : undefined;
}

const getData = () => {
    return localStorage.getItem('data') !== null ? localStorage.getItem('data') : undefined;
}

const isNumber = ( num ) => {
    return typeof num === 'number';
}

const isObject = ( obj ) => {
    return typeof obj === 'object';
}

export {
    firstStart
}