import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group, Div } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import './style.css';

const osname = platform();

const Shop = props => {	

	const [score, setScore] = useState(parseInt(localStorage.getItem('score')));
	const [items, setItems] = useState(JSON.parse(localStorage.getItem('items')));
	const [prices, setPrices] = useState(JSON.parse(localStorage.getItem('prices')));
	const [data, setData] = useState(JSON.parse(localStorage.getItem('data')));

	const buy = ( item ) => {
		if(getScore() - prices[item].price > 0) {
			saveScore(-prices[item].price);
			setItems(Object.assign(items,{ [item]: items[item] + 1 }));
			setPrices(Object.assign(prices, { [item]: Object.assign(prices[item], { price: Math.round(prices[item].price * 1.1)})}));
			setData(Object.assign(data,{ auto: data.auto + prices[item].boost }));
			localStorage.setItem('items', JSON.stringify(items));
			localStorage.setItem('prices', JSON.stringify(prices));
			localStorage.setItem('data', JSON.stringify(data));
		}
	}

	const getScore = () => {
		return parseInt(localStorage.getItem('score'));
	}
  
	const saveScore = ( additional ) => {
	  setScore(getScore() + additional);
	  localStorage.setItem('score', getScore() + additional);
	}

	const locale = {
		farm: 'Ферма',
		factory: 'Фабрика',
		mine: 'Шахта',
		country: 'Страна'
	}

	return(
		<Panel id={props.id}>
			<PanelHeader>
				Магазин
			</PanelHeader>
			<Group>
				<Div className="shop-score">
					{score}
				</Div>
			</Group>
			{Object.keys(items).map((item) => (
						<Group key={item}>
							<Div onClick={() => buy(item)} className="shop-item">
								<h4>{locale[item]}</h4> <p>Цена: {prices[item].price} | Кол-во: {items[item]} | +{prices[item].boost}/с </p>             
							</Div>
						</Group>
					))
			}
		</Panel>
	);
}

export default Shop;
