import React, { useState, useEffect } from 'react'
import {Panel, PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { ListItem, Button, Group, Div, Avatar, FixedLayout, Tabs, TabsItem } from '@vkontakte/vkui';
import './style.css'

const osname = platform();

const Default = props => {

    const [timer, setTimer] = useState();
    const [score, setScore] = useState(parseInt(localStorage.getItem('score')));
    const [data, setData] = useState(JSON.parse(localStorage.getItem('data')));

    useEffect(() => {
        setInterval(() => {
            autoAdd();
        },1000);
    }, []);

    const add = () => {
        saveScore(data.perClick);
    }
  
  // Cброс счёта
  /* const resetScore = (obj) => {
    for (var prop in obj) {
        if (obj.hasOwnProperty(prop)) {
            obj[prop] = { string: '', number: 0 }[typeof obj[prop]];
        }
    }
  } */

  // Автоматическое добавление в секунду
  const autoAdd = () => {
    if(typeof score !== 'object') {
        saveScore(data.auto);
        setTimer(!timer);
    }
  }

  const getScore = () => {
      return parseInt(localStorage.getItem('score'));
  }

  const saveScore = ( additional ) => {
    setScore(getScore() + additional);
    localStorage.setItem('score', getScore() + additional);
  }

    return (
      <Panel id={props.id}>
        		<PanelHeader>
                    Clicker
		        </PanelHeader>
                <Group>
                        <Div className="score-container">
                            <p>{score}#</p>
                        </Div>    
                </Group>
                <Group>
                    <Div className="button-container">
                        <button className="button-click" onClick={() => add()}>
                            <h1>H+</h1>
                            <span>{data.auto} / сек</span>
                        </button>
                    </Div>
                </Group>
      </Panel>
    )
}
  
  export default Default
