import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, FixedLayout, Tabs, TabsItem } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Shop from './panels/Shop/Shop';
import Default from './panels/Default/Default';
import { firstStart } from './core/main';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'default',
			fetchedUser: null,
		};
		firstStart();
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<div>
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Shop id="shop" go={this.go} />
				<Default id="default" go={this.go} />
			</View>
			<FixedLayout vertical="bottom">
              <Tabs>
                <TabsItem
                  onClick={(e) => this.go(e)}
                  data-to="default"
                  selected={this.state.activePanel === 'default'}
                >Главная</TabsItem>
                <TabsItem
                  onClick={(e) => this.go(e)}
                  data-to="shop"
                  selected={this.state.activePanel === 'shop'}
                >Магазин</TabsItem>
              </Tabs>
            </FixedLayout>  
			</div>
		);
	}
}

export default App;
